import React, { useEffect, useState, useRef } from "react";
import { useReactToPrint } from "react-to-print";
import DefaultLayout from "../components/DefaultLayout";
import { EyeOutlined } from "@ant-design/icons";
import ReactToPrint from "react-to-print";
import { Table } from "antd";
import axios from "axios";
import { Modal, Button } from "antd";
import { useDispatch } from "react-redux";
import "../styles/InvoiceStyles.css";

const BillsPage = () => {
  const componentRef = useRef();
  const [billsData, setBillsData] = useState([]);
  const [popupModal, setPopupModal] = useState(false);
  const [selectedBill, setSelectedBill] = useState(null);
  const dispatch = useDispatch();

  const getAllBills = async () => {
    try {
      dispatch({
        type: "SHOW_LOADING",
      });
      const { data } = await axios.get("/api/bills/get-bills");
      setBillsData(data);
      dispatch({
        type: "HIDE_LOADING",
      });
      console.log(data);
    } catch (error) {
      dispatch({
        type: "HIDE_LOADING",
      });
      console.log(error);
    }
  };

  useEffect(() => {
    getAllBills();
  });

  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  });

  const columns = [
    { title: "ID", dataIndex: "_id" },
    {
      title: "Customer Name",
      dataIndex: "customerName",
    },
    { title: "Customer Number", dataIndex: "customerNumber" },
    { title: "Subtotal", dataIndex: "subTotal" },
    { title: "Total Amount", dataIndex: "totalAmount" },
    { title: "Tax", dataIndex: "tax" },

    {
      title: "Actions",
      dataIndex: "_id",
      render: (id, record) => (
        <div>
          <EyeOutlined
            style={{ cursor: "pointer" }}
            onClick={() => {
              setSelectedBill(record);
              setPopupModal(true);
            }}
          />
        </div>
      ),
    },
  ];
  return (
    <DefaultLayout>
      <div className="d-flex justify-content-between">
        <h1>Invoice List</h1>
      </div>

      <Table columns={columns} dataSource={billsData} />

      {popupModal && (
        <Modal

          width = {400}
            
          title= "invoice Details"
          open={popupModal}
          onCancel={() => setPopupModal(false)}
          footer={false}
        >
          <div id = "invoice-POS">
            <center id = "top">
              <div className = "logo"/>

              <div className = "info">

              <h2> Tech-Info YT POS</h2>
              <p> Contact: 123456 | Pakistan</p>

              </div>

            </center>
          </div>


          <div id = "mid">
           <div className = "mt-2">

            <p>

              Customer Name:<b> {selectedBill.customerName}</b>
              <br/>
              Phone No: <b>{selectedBill.customerNumber}</b>
              <br/>

              Date : <b>{selectedBill.date.toString().substring(0, 10)}</b>

              <br/>

            </p>

          <hr style = {{margin: "5px"}}/>


        </div>
        </div>


        <div id = "bot">
          <div id = "table">
            <table>

              <tbody>

                <tr className = "tabletitle">
                  <td className="item">
                    <h2>Item</h2>

                  </td>

                  <td className="Hours">
                    <h2>Qty</h2>

                  </td>

                  <td className="Rate">
                    <h2>Price</h2>

                  </td>

                  <td className="Rate">
                    <h2>Total</h2>

                  </td>


                </tr>


              </tbody>
            </table>

          </div>


        </div>


        


          

        
          
        </Modal>
      )}
    </DefaultLayout>
  );
};

export default BillsPage;
